<template>
  <div id="app">
    <h1>Editable </h1>
    <el-tabs v-model="activeName">
      <el-tab-pane label=" mini" name="EditableDemo1"></el-tab-pane>
      <el-tab-pane label=" small" name="EditableDemo2"></el-tab-pane>
      <el-tab-pane label=" " name="EditableDemo3"></el-tab-pane>
      <el-tab-pane label=" medium" name="EditableDemo4"></el-tab-pane>
      <el-tab-pane label=" medium" name="EditableDemo5"></el-tab-pane>
      <el-tab-pane label="" name="EditableDemo6"></el-tab-pane>
      <el-tab-pane label="" name="EditableDemo7"></el-tab-pane>
      <el-tab-pane label="" name="EditableDemo8"></el-tab-pane>
      <el-tab-pane label="" name="EditableDemo9"></el-tab-pane>
      <el-tab-pane label="" name="EditableDemo10"></el-tab-pane>
      <el-tab-pane label=" " name="EditableDemo11"></el-tab-pane>
      <el-tab-pane label=" " name="EditableDemo12"></el-tab-pane>
      <el-tab-pane label="Excel1" name="EditableExcelDemo1"></el-tab-pane>
      <el-tab-pane label="Excel2" name="EditableExcelDemo2"></el-tab-pane>
      <el-tab-pane label="Excel3 " name="EditableExcelDemo3"></el-tab-pane>
    </el-tabs>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    activeName: {
      get () {
        return this.$route.name
      },
      set (value) {
        this.$router.replace({name: value})
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>



